body {
  margin: 0;
  padding: 0;
  font-family: system-ui, -apple-system, 'SF Pro Display', 'Segoe UI',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-attachment: fixed;
  background-position-x: center;
  background-position-y: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #f0f0f0;
  /*background-color: #324878;*/
  text-shadow: var(--secondary-text-shadow);
  /*background-image: url(/bg-light.jpg);
  transition: background 0.2s linear;*/
  transition: background-image 0.5s linear;
}

body * {
  visibility: hidden;
}

body.theme-blue *,
body.theme-light *,
body.theme-dark * {
  visibility: inherit;
}

body.theme-blue {
  background-color: #324878;
  background-image: url(/bg-blue.jpg);
}
body.theme-blue.searchFocused {
  background-image: url(/bg-blue-blurred.jpg);
}

body.theme-light {
  background-color: #f0f0f0;
  background-image: url(/bg-light.jpg);
}
body.theme-light.searchFocused {
  background-image: url(/bg-light-blurred.jpg);
}

body.theme-dark {
  background-color: #404040;
  background-image: url(/bg-dark.jpg);
}
body.theme-dark.searchFocused {
  background-image: url(/bg-dark-blurred.jpg);
}
