/* Theme styles */

.theme-blue {
  --overlay-background: #1b263f;
  --highlight-background: #0004;
  --icon-filter: initial;
  --black-icon-filter: invert(62%) sepia(98%) saturate(12%) hue-rotate(175deg)
    brightness(90%) contrast(96%);
  --primary-color: white;
  --secondary-color: #fff9;
  --tertiary-color: #fff6;
  --divider-color: #fff1;
  --primary-text-shadow: 0 0 3px #0002, 0 0 12px #0004;
  --secondary-text-shadow: 0 0 3px #0004;
}

.theme-dark {
  --overlay-background: #202327;
  --highlight-background: #0004;
  --icon-filter: initial;
  --black-icon-filter: invert(62%) sepia(98%) saturate(12%) hue-rotate(175deg)
    brightness(90%) contrast(96%);
  --primary-color: white;
  --secondary-color: #fff9;
  --tertiary-color: #fff6;
  --divider-color: #fff1;
  --primary-text-shadow: 0 0 3px #0002, 0 0 12px #0004;
  --secondary-text-shadow: 0 0 3px #0004;
}

.theme-light {
  --overlay-background: #d0d0d0;
  --highlight-background: #b9b9b9;
  --icon-filter: brightness(0);
  --black-icon-filter: invert(39%) sepia(21%) saturate(200%) saturate(109.5%)
    hue-rotate(174deg) brightness(94%) contrast(86%);
  --primary-color: #1c1e22;
  --secondary-color: #5f6670;
  --tertiary-color: #0006;
  --divider-color: #0001;
  --primary-text-shadow: 0 0 3px #fff2, 0 0 12px #fff4;
  --secondary-text-shadow: 0 0 3px #fff4;
}

.theme-blue .overlayBackground {
  background-color: #1b263f;
}
.theme-dark .overlayBackground {
  background-color: #202327;
}
.theme-light .overlayBackground {
  background-color: #d0d0d0;
}

.theme-light img.icon {
  filter: brightness(0);
}

.secondaryColor {
  color: var(--secondary-color);
}

.primaryColor,
.primaryColor a {
  color: var(--primary-color);
}

.theme-light .overlayBackground.secondaryColor,
.theme-light .overlayBackground .secondaryColor {
  color: #9fa0a1;
}

.theme-blue .divider {
  background: #4e5d7b;
}

.theme-dark .divider {
  background: #5b5e61;
}

.theme-light .divider {
  background: #b8bcc0;
}

.theme-blue .borderColor {
  border-color: #4e5d7b;
}

.theme-dark .borderColor {
  border-color: #5b5e61;
}

.theme-light .borderColor {
  border-color: #b8bcc0;
}

/* End Theme Styles */

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: min-content min-content 1fr 1fr min-content;
  grid-template-columns: min-content 1fr;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-weight: 300;
  grid-row: 3;
  grid-column: 2;
  margin-right: 36px;
}

.calendarUpNextWrapper {
  grid-column: 2;
  grid-row: 4;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-right: 36px;
}

.time {
  margin-top: 40px;
  font-size: 144px;
  font-weight: 100;
  position: relative;
  text-shadow: var(--primary-text-shadow);
}

.timeAMPM {
  font-size: 48px;
  font-weight: 200;
  letter-spacing: 2px;
  font-weight: 300;
  position: absolute;
  bottom: 56px;
  margin-left: 13px;
  text-shadow: var(--primary-text-shadow);
}

.message {
  font-size: 56px;
  font-weight: 200;
  text-shadow: var(--primary-text-shadow);
}

.calendarWrapper {
  height: 90px;
  bottom: 15%;
  display: flex;
  justify-content: center;
  width: 100%;
}

.calendarLeftChevron {
  left: -40px;
}

.calendarRightChevron {
  right: -40px;
}

.calendarChevron {
  opacity: 0.5;
  position: absolute;
  border: 0;
  background: #0000;
  padding: 0;
  cursor: pointer;
  height: 100%;
  width: 40px;
}

.calendarChevron > img {
  width: 16px;
  height: 16px;
}

.calendarWash {
  border-radius: 10px;
  position: absolute;
  left: -40px;
  right: -40px;
  height: 100%;
  top: 0;
  opacity: 0.8;
}

.hourCells {
  position: absolute;
  font-size: 10px;
  font-weight: 500;
  top: 10px;
  display: flex;
  left: 0;
  right: 0;
  justify-content: space-between;
  border-right: 1px solid #4c66a2;
}

.hourCell {
  display: flex;
  flex-grow: 1;
  flex-basis: 0;
}

.hour {
  padding-left: 5px;
}

.divider {
  background: #4e5d7b;
  width: 1px;
  height: 70px;
}

.currentTime {
  background: #d68d4a;
  width: 2px;
  height: 90px;
  position: absolute;
  pointer-events: none;
}

.currentTimeBall {
  background: #d68d4a;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  position: absolute;
  margin-left: -4px;
  margin-top: -4px;
}

.upNextWrapper.upNextWrapperHidden {
  visibility: hidden;
}

.upNextWrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.upNext {
  font-size: 16px;
  line-height: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 300;
}

.upNextLabel {
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 500;
}

.earlyMeetingWrapper.earlyMeetingWrapperHidden {
  visibility: hidden;
}

.earlyMeetingWrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.earlyMeeting {
  font-size: 16px;
  line-height: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 300;
}

.earlyMeetingLabel {
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 1px;
  font-weight: 500;
}

.event {
  position: absolute;
  height: 40px;
  top: 30px;
  background: #3a5aa1;
  border-radius: 5px;
  opacity: 0.8;
  box-sizing: border-box;
  margin: 1px;
}

.event:hover {
  border: 1px solid #ffffff80;
  opacity: 1;
}

.theme-dark .event {
  opacity: 0.6;
}

.theme-light .event {
  opacity: 0.5;
}

.theme-dark .event:hover {
  opacity: 0.8;
}

.theme-light .event:hover {
  opacity: 0.75;
}

.eventPopover {
  border: 1px solid #ffffff5f;
  border-radius: 8px;
  opacity: 1;
  padding: 12px;
  font-size: 14px;
}

.tippyPopover {
  background: unset;
}

.tippyPopover .tippy-content {
  padding: 0;
}

.tippyPopover .tippy-arrow {
  color: #ffffff5f;
}

.eventPurple {
  background-color: #8c6ece;
  opacity: 0.6;
}

.eventDarkPurple {
  background-color: #58409b;
  opacity: 0.6;
}

.eventTeal,
.eventDarkTeal {
  background-color: #4cc6ee;
  opacity: 0.6;
}

.eventOlive,
.eventGreen,
.eventDarkGreen,
.eventDarkOlive {
  background-color: #a4d171;
  opacity: 0.6;
}

.eventRed,
.eventDarkRed,
.eventDarkMaroon {
  background-color: #f55166;
  opacity: 0.6;
}

.eventMaroon {
  background-color: #ec7ebd;
  opacity: 0.6;
}

.eventOrange,
.eventPeach,
.eventDarkPeach {
  background-color: #fd7043;
  opacity: 0.6;
}

.eventDarkOrange {
  background-color: #c32d0e;
  opacity: 0.6;
}

.eventYellow,
.eventDarkYellow {
  background-color: #f5c424;
  opacity: 0.6;
}

.eventSteel,
.eventGray,
.eventDarkGray {
  background-color: #7f7f7f;
  opacity: 0.6;
}

.eventBlack {
  background-color: #111;
  opacity: 0.6;
}

.eventPink {
  background-color: #ee7bbe;
  opacity: 0.6;
}

.eventPopoverTitle {
  font-weight: bold;
  font-size: 16px;
}

.eventPopoverTime {
  margin-top: 4px;
  white-space: nowrap;
  margin-bottom: 4px;
}

.eventPopoverAttendees {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  margin-top: 12px;
  padding-left: 8px;
}

.eventPopoverAttendee {
  border-radius: 50%;
  border: 2px solid white;
  overflow: hidden;
  width: 32px;
  height: 32px;
  flex-shrink: 0;
  margin-left: -8px;
}

.eventPopoverAttendee > img {
  width: 32px;
  height: 32px;
}

.eventPopoverAttendeeExtra {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 10px;
}

.errorWrapper {
  align-items: center;
  border-radius: 7px;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  height: 250px;
  justify-content: space-around;
  padding: 10px 20px;
  text-align: center;
  width: 60%;
}

.errorTitle {
  color: white;
  font-size: 32px;
  position: relative;
}

.errorMessage {
  color: #ddd;
  font-size: 16px;
  position: relative;
}

.errorMessage a {
  color: #ddd;
}

.settingsClose {
  border: 0;
  background: none;
  cursor: pointer;
}

.settingsRadioContainer {
  display: flex;
  cursor: pointer;
  position: relative;
  align-items: center;
  font-size: 13px;
}

.settingsRadioCheckmark {
  border-radius: 50%;
  border: 1px solid white;
  margin-right: 8px;
  width: 20px;
  height: 20px;
  box-sizing: border-box;
}

.settingsRadioCheckmarkBlue {
  background-color: #4065b4;
}

.settingsRadioCheckmarkLight {
  background-color: #dadde1;
}

.settingsRadioCheckmarkDark {
  background-color: #444950;
}

.settingsRadioContainerChecked {
  font-weight: bold;
}

.settingsRadioContainerChecked > .settingsRadioCheckmark {
  border-width: 2px;
}

.settingsRadioCheckmark:after {
  content: '';
  position: absolute;
  display: none;
  top: 6px;
  left: 6px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
}

.settingsRadioContainerChecked .settingsRadioCheckmark:after {
  display: block;
}

/* Hide things when searching */
.searchFocused .message,
.searchFocused .time,
.searchFocused .upNextWrapper,
.searchFocused .calendarWrapper {
  visibility: hidden;
  transition: all 0.25s ease-in-out;
}

.searchWrapper {
  margin-top: 20px;
  opacity: 0.8;
  transition: all 0.25s ease-in-out;
}

.searchFocused .searchWrapper {
  margin-top: 0;
  position: fixed;
  top: 10%;
  transition: all 0.4s ease-in-out;
}

.searchWithSuggestions {
  position: relative;
  z-index: 1;
  transition: all 0.4s ease-in-out;
}

.searchFocused .searchHasText {
  background: var(--overlay-background);
  padding: 4px 24px 0px 24px;
  border-radius: 24px;
  transition: all 0.4s ease-in-out;
}

.searchResultHighlight {
  font-weight: 500;
}

.searchCloseButton {
  display: none;
  position: fixed;
  top: 32px;
  right: 32px;
}

.searchFocused .searchCloseButton {
  display: block;
}

.switch {
  position: relative;
  display: block;
  float: right;
  left: 0px;
  width: 36px;
  height: 20px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #6664;
  transition: 0.4s;
  border: 1px solid white;
  border-radius: 12px;
}

.slider:before {
  position: absolute;
  content: '';
  height: 12px;
  width: 12px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  box-shadow: 0 0 3px #0006;
  transition: 0.4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: #3338;
}

input:focus + .slider {
  box-shadow: 0 0 3px #0002;
}

input:checked + .slider:before {
  transform: translateX(16px);
}

.uploadNotice {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.85);
  color: white;
  align-items: center;
  justify-content: center;
  font-size: 64px;
  display: none;
}

.isUploading .uploadNotice {
  display: flex;
}
